.fog {
  position: absolute;
  height: 100%;
  width: 100%;
  overflow: hidden;
  top: 0;
  left: 0;
  filter: grayscale(110) contrast(0.2);
  opacity: 0.6;

  &,
  &::after,
  &::before {
    box-sizing: inherit;
  }

  .layer {
    position: absolute;

    height: 100vh;
    width: 300vw;
  }

  .first-layer {
    background: url("https://res.cloudinary.com/dkr52htco/image/upload/v1536173269/fog-1.png")
      repeat-x;
    background-size: contain;
    background-position: center;
    animation: marquee 300s linear infinite;
  }

  .second-layer {
    background: url("https://res.cloudinary.com/dkr52htco/image/upload/v1536173269/fog-1.png")
      repeat-x;
    background-size: contain;
    background-position: center;
    animation: marquee 300s linear infinite;
  }
}

// Marquee
@keyframes marquee {
  0% {
    transform: translate3d(0, 0, 0);
  }
  100% {
    transform: translate3d(-200vw, 0, 0);
  }
}
