header {
  height: 60px;
  width: 100%;
  padding: 0px 13px;
  border-bottom: 3px solid #fff;
  font-family: "Open Sans", sans-serif;
  color: #fff;
  z-index: 99;
  position: absolute;
  top: 0;
  left: 0;
  .nav-bar {
    width: 100%;
    height: 100%;
    .nav-bar-container {
      display: flex;
      height: 100%;
      align-items: flex-end;
      ul {
        list-style: none;
        display: flex;
        margin: 11px 0;
        .nav-item {
          margin-left: 26px;
          font-size: 19px;
          font-weight: 700;
          &:first-child {
            margin-left: 6px;
          }
        }
      }
      .line-selector {
        width: 55px;
        height: 2px;
        background-color: #fff;
        position: absolute;
        top: 48px;
        transition: all 450ms ease-in-out;
      }
    }
  }
}
