.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Pages */
.page {
  height: 100vh;
  width: 100%;
  margin: 0;
  display: flex;
  flex-direction: column;
  color: #fff;
  position: absolute;
  top: 3em;
  padding: 17px;
  .page-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  transition: all 1.3s ease-in-out;
}

.header {
  margin-top: 3em;
  font-size: 25px;
  color: #fff;
}

.particles-container {
  background-color: #29292929;
  width: 100%;
  height: 100vh;
}

.particles-canvas {
  width: 100%;
  height: 100%;
  filter: brightness(2);
}

//Page Transition
.page-transition-enter {
  opacity: 0;
}

.page-transition-enter-active {
  opacity: 1;
}

.page-transition-enter-done {
  opacity: 1;
}

.page-transition-exit {
  opacity: 1;
}

.page-transition-exit-active {
  opacity: 0;
}

.page-transition-exit-done {
  opacity: 1;
}

//Button
.btn {
  height: 30px;
  font-size: 15px;
  border-radius: 3px;
  border: none;
  background-color: #fff;
  font-family: "Open Sans", sans-serif;
  outline: none;
  transition: all 350ms ease-in-out;
  cursor: pointer;

  &.btn-submit {
    background-color: #2980b9;
    color: #fff;
    &:hover {
      transform: translateY(2px);
      box-shadow: 0px 0px 15px 2px rgba(255, 255, 255, 0.2);
    }
  }
}

//Form Error
.form-error {
  margin-top: 5px;
  color: #e7412e;
  font-size: 15px;
  font-family: "Open Sans", sans-serif;
}
