.moon {
  position: absolute;
  right: 11em;
  top: 13.4em;
  margin: -100px;
  width: 110px;
  height: 110px;
  background: #ececec;
  border-radius: 50%;
  transition: transform 0.2s ease-in-out;
  z-index: 99;
  box-shadow: inset -25px 0px 0 0px #f6f6f6, 0px 0px 48px 10px #ffffff;
  cursor: pointer;
  transition: all 280ms ease-in-out;
  &:hover {
    filter: contrast(0.8);
  }
  animation: wave_animation 4s infinite ease-in-out;

  .moon-inner-container {
    li {
      position: absolute;
      list-style: none;
      background: #bfbec3a8;
      border-radius: 50%;
      position: relative;
    }

    li:nth-child(1) {
      left: -31px;
      top: 21px;
      width: 22px;
      height: 23px;
      box-shadow: inset 6px -2px 0 0px #acaaaa78;
    }
    li:nth-child(2) {
      left: 25px;
      top: -10px;
      width: 17px;
      height: 17px;
      box-shadow: inset 6px -2px 0 0px #acaaaa78;
    }

    li:nth-child(3) {
      left: 5px;
      top: 24px;
      width: 17px;
      height: 16px;
      box-shadow: inset 6px -2px 0 0px #acaaaa78;
    }

    li:nth-child(4) {
      left: -22px;
      top: 1px;
      width: 10.5px;
      height: 9.5px;
      box-shadow: inset 6px -2px 0 0px #acaaaa78;
    }

    li:nth-child(5) {
      left: -2.5px;
      top: -70.333333px;
      width: 9.5px;
      height: 9.5px;
      box-shadow: inset 6px -2px 0 0px #acaaaa78;
    }
    li:nth-child(6) {
      left: 6.285714px;
      top: -42px;
      width: 8.5px;
      height: 8.5px;
      box-shadow: inset 6px -2px 0 0px #acaaaa78;
    }

    li:nth-child(7) {
      left: 51.818182px;
      top: -44px;
      width: 9.5px;
      height: 9.5px;
      box-shadow: inset 6px -2px 0 0px #acaaaa78;
    }
  }
}

//Moon wave animation
@keyframes wave_animation {
  0% {
    top: 13.4em;
    rotate: 0deg;
  }

  50% {
    top: 13.6em;
    rotate: 180deg;
  }

  100% {
    top: 13.4em;
    rotate: 360deg;
  }
}
