.projects {
  width: 60%;
  max-height: 600px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 3em;
  overflow-y: auto;
  &::-webkit-scrollbar {
    width: 3px;
  }

  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px rgba(15, 15, 15, 0.4);
    border-radius: 15px;
  }

  &::-webkit-scrollbar-thumb {
    background: #fff;
    border-radius: 8px;
  }
}

.project {
  width: 13em;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2em;
  &:nth-child(1) &:nth-child(2) {
    margin-top: 0;
  }
  flex: 0 50%;
  .container {
    .image {
      width: 18em;
      height: 13em;
      box-shadow: 0px 0px 15px 4px rgba(15, 15, 15, 0.4);
      position: relative;
      padding: 2px;
      background-color: #fff;
      border-radius: 4px;
      img {
        width: 100%;
        height: 100%;
        border-radius: 4px;
      }
      &:hover {
        .details {
          opacity: 1;
          visibility: visible;
        }
      }
      .details {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0px;
        visibility: 0;
        opacity: 0;
        transition: all 450ms ease-in-out;
        background-color: rgba(255, 255, 255, 0.9);
        border-radius: 4px;
        color: #272729;
        padding: 10px;
        line-height: 20px;
        cursor: pointer;
      }
    }
  }

  .footer {
    width: 100%;
    font-size: 20px;
    text-align: center;
    margin-top: 20px;
    text-shadow: 0px 0px 12px rgba(15, 15, 15, 0.4);
    font-size: 18px;
  }
}
