.social {
  min-width: 27em;
  height: 3em;
  margin-top: 18px;
  .social-container {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    .link {
      width: 38px;
      height: 35px;
      //background-color: #218c74;
      background-color: #ff5252;
      margin-right: 25px;
      display: flex;
      justify-content: center;
      align-items: center;
      box-shadow: 0px 0px 9px 1px rgba(15, 15, 15, 0.2);
      border-radius: 4px;
      cursor: pointer;
      transition: all, 250ms ease-in-out;
      &:last-child {
        margin-right: 0;
      }
      &:hover {
        background-color: transparent;
        box-shadow: none;
        transform: translateY(2px);
      }
    }

