.main-profile {
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: transform, 1s ease-in-out;
  z-index: 99;
  .my-title {
    font-size: 17px;
  }
}

.more-details {
  display: flex;
  height: 25em;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 7em;
  margin-left: 15px;
  border-left: 2px solid #fff;
  padding: 5px 25px;
  transition: all 1s, opacity 1s;
  transition-timing-function: ease-in-out;
  z-index: 10;
  max-width: 30em;

  .details-header {
    position: relative;
    width: 100%;
    height: 5px;
    .close-icon {
      position: absolute;
      top: -24px;
      left: -31px;
      color: #fff;
      cursor: pointer;
      transition: color 180ms ease-in-out;
      &:hover {
        color: #ff5252;
      }
    }
  }

  .container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    .item {
      margin-bottom: 25px;
      margin-left: 15px;
      color: #fff;
      font-size: 17px;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      text-align: start;
    }

    .highlight {
      color: #e74848;
    }
  }
}

//Details Animatiomn
.details-anim-enter {
  opacity: 0;
  position: absolute;
  transform: translateX(5em);
}

.details-anim-enter-active {
  opacity: 1;
  position: absolute;
  transform: translateX(18em);
}

.details-anim-enter-done {
  opacity: 1;
  position: absolute;
  transform: translateX(18em);
}

.details-anim-exit {
  opacity: 1;
  position: absolute;
  transform: translateX(18em);
}

.details-anim-exit-active {
  opacity: 0;
  position: absolute;
  transform: translateX(5em);
}

.details-anim-exit-done {
  opacity: 0;
  position: absolute;
  transform: translateX(5em);
}
