.contact {
  max-width: 40em;
  margin-top: 5em;
  font-family: "Open Sans", sans-serif;
  .form {
    display: flex;
    flex-direction: column;
    align-items: center;
    .form-group {
      display: flex;
      justify-content: center;
      align-content: center;
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 2em;
      label {
        font-size: 22px;
        margin-bottom: 15px;
      }

      input {
        width: 20em;
        height: 34px;
        border: 0;
        //box-shadow: 0px 0px 20px 2px #2980b991;
        box-shadow: 0px 0px 20px 2px #ffffff2f;
        transition: all 300ms ease-in-out;
      }

      textarea {
        max-width: 30em;
        width: 20em;
        height: 100px;
        box-shadow: rgba(255, 255, 255, 0.184) 0px 0px 20px 2px;
        transition: all 300ms ease-in-out 0s;
        margin: 0px;
        resize: none;
        &:focus {
          height: 250px;
        }
      }

      input,
      textarea {
        padding: 0 8px;
        font-size: 16px;
        border-radius: 3px;
        &:focus {
          box-shadow: 0px 0px 20px 2px #ffffff63;
        }
      }
    }
  }
}
