.progress-bar {
  width: inherit;
  height: 32px;
  .progress-bar-container {
    border: 3px solid #f2efef;
    padding: 5px;
    width: 21em;
    height: 100%;
    display: flex;
    align-items: center;
    border-radius: 4px;
    position: relative;
    .inner-progress {
      background-color: #fff;
      width: 80%;
      height: 111%;
    }
    .percentage-text {
      color: #000;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}
