.skills {
  .skills-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 6em;
    .side {
      min-width: 30em;
      max-height: 30em;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      margin-right: 25px;
      justify-content: flex-start;
      border-right: 2px solid #fff;
      padding: 5px 0;
      padding-right: 35px;
      &:last-of-type {
        border: 0;
        padding-left: 5px;
      }

      .skill {
        display: flex;
        justify-content: space-between;
        margin-top: 40px;
        .name {
          color: #fff;
          font-size: 19px;
          font-weight: 600;
          text-shadow: 1px 1px 12px rgba(15, 15, 15, 0.65);
          line-height: 1.5;
          font-family: "Open Sans";
        }

        .progress {
          margin-left: 2em;
          width: 20em;
        }
      }
    }
  }
}
