.my-thumbnail {
  display: flex;
  height: fit-content;
  margin-top: 7em;
  margin-bottom: 1em;
  width: fit-content;
  transition: all, 300ms ease-in-out;
  z-index: 99;
  .my-thumbnail-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    .image {
      width: 13.3em;
      height: 13em;
      margin-bottom: 30px;
      position: relative;
      cursor: pointer;
      &.show {
        &:hover {
          .details {
            visibility: visible;
            opacity: 1;
          }
          img {
            filter: blur(1.6px) brightness(0.9);
          }
        }
      }
      .details {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: fit-content;
        visibility: hidden;
        opacity: 0;
        transition: all, 250ms ease-in-out;
      }
      img {
        border-radius: 50%;
        width: 100%;
        height: 100%;
        padding: 4px;
        background: #fff;
        box-shadow: 0px 5px 8px 5px rgba(15, 15, 15, 0.2);
        transition: all, 250ms ease-in-out;
      }
    }
    .name {
      font-size: 25px;
      font-weight: 500;
    }

    .aka {
      margin-top: 5px;
      font-size: 18px;
      b {
        color: #e74848;
        margin-right: 6px;
      }
    }
    .email {
      margin-top: 20px;
      font-size: 18px;
      b {
        color: #e74848;
        margin-right: 6px;
      }
    }
  }
}
